export default class AppView {
  constructor(titles, images, chTitles, date, views, description, id, maxRes) {
    this.titles = titles;
    this.images = images;
    this.chTitles = chTitles;
    this.date = date;
    this.views = views;
    this.description = description;
    this.id = id;
    this.maxRes = maxRes;
  }

  static addMetaTag() {
    const head = document.querySelector('head');
    const meta = document.createElement('meta');

    meta.name = 'viewport';
    meta.content = 'width=device-width, initial-scale=1';
    head.appendChild(meta);
  }

  static createInput() {
    const input = document.createElement('input');
    input.type = 'text';
    input.placeholder = 'Find video';
    input.id = 'search';
    input.className = 'search';
    document.body.appendChild(input);
  }

  static createContainers() {
    const container = document.createElement('div');
    container.className = 'container';
    container.id = 'container';
    document.body.appendChild(container);

    const vContainer = document.createElement('div');
    vContainer.className = 'video-container';
    vContainer.id = 'video-container';
    container.appendChild(vContainer);
  }

  static createButtons() {
    const buttonsField = document.createElement('div');
    buttonsField.className = 'buttons-field';
    document.body.appendChild(buttonsField);

    const buttonLeft = document.createElement('button');
    buttonLeft.className = 'button-left';
    buttonsField.appendChild(buttonLeft);

    const buttonLeftImg = document.createElement('img');
    buttonLeftImg.className = 'button-left-img';
    buttonLeftImg.src = '/./youtube/assets/images/back.png';
    buttonLeft.appendChild(buttonLeftImg);

    const currentStateCircle = document.createElement('div');
    currentStateCircle.className = 'stateCircle';
    buttonsField.appendChild(currentStateCircle);

    const tooltipCenter = document.createElement('span');
    tooltipCenter.className = 'tooltip-center';
    currentStateCircle.appendChild(tooltipCenter);

    const buttonRight = document.createElement('button');
    buttonRight.className = 'button-right';
    buttonsField.appendChild(buttonRight);

    const buttonRightImg = document.createElement('img');
    buttonRightImg.className = 'button-right-img';
    buttonRightImg.src = '/./youtube/assets/images/right-arrow.png';
    buttonRight.appendChild(buttonRightImg);
  }

  render() {
    for (let i = 0; i < this.maxRes; i += 1) {
      const clips = document.createElement('div');
      clips.className = 'clips';
      document.getElementById('video-container').appendChild(clips);

      const containers = document.createElement('div');
      containers.className = 'clip-names-back';
      clips.appendChild(containers);

      const titlesURL = document.createElement('a');
      titlesURL.className = 'clip-names-url';
      titlesURL.href = `https://www.youtube.com/watch?v=${this.id}`;
      titlesURL.target = '_blank';
      containers.appendChild(titlesURL);

      const titles = document.createElement('h2');
      titles.innerHTML = this.titles[i];
      titles.className = 'clip-names';
      titlesURL.appendChild(titles);

      const image = document.createElement('img');
      image.src = this.images[i];
      image.className = 'clip-image';
      clips.appendChild(image);

      const chImage = document.createElement('img');
      chImage.src = '/./youtube/assets/images/man-user.png';
      chImage.className = 'clip-chImage';
      clips.appendChild(chImage);

      const chNames = document.createElement('h3');
      chNames.className = 'clip-chNames';
      chNames.innerHTML = this.chTitles[i];
      clips.appendChild(chNames);

      const calendar = document.createElement('img');
      calendar.src = '/./youtube/assets/images/calendar.png';
      calendar.className = 'clip-calendar';
      clips.appendChild(calendar);

      const date = document.createElement('p');
      date.innerHTML = (this.date[i]).slice(0, 10);
      date.className = 'clip-date';
      clips.appendChild(date);

      const eye = document.createElement('img');
      eye.src = '/./youtube/assets/images/eye.png';
      eye.className = 'clip-eye';
      clips.appendChild(eye);

      const view = document.createElement('p');
      view.innerHTML = this.views[i];
      view.className = 'views';
      clips.appendChild(view);

      const description = document.createElement('p');
      description.className = 'descr';
      description.innerHTML = this.description[i];
      clips.appendChild(description);
    }
  }

  static delete() {
    document.getElementsByClassName('container')[0].remove();
  }

  static deleteButtons() {
    document.querySelector('.buttonsField').remove();
  }
}
