import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

export default class App {
  constructor() {
    this.state = {
      url: 'https://www.googleapis.com/youtube/v3/',
    };
  }

  static getApiKey() {
    return 'key=AIzaSyCUc9mTS15ZSS2RbWhg--siK6kQ_Km6T4M';
  }

  static getInputValue() {
    return document.getElementById('search').value;
  }

  static setMaxResults() {
    return 15;
  }

  static renderNewVideos(token) {
    const slider = document.getElementById('container');
    let maxRes = App.setMaxResults();
    let cardsForFour = 4;
    let cardsForThree = 3;
    let cardsForTwo = 2;
    let cardsForOne = 1;
    if (slider.offsetWidth === 1280 && maxRes <= 60) {
      if (Math.round((maxRes - (Math.round(slider.scrollLeft
        / slider.offsetWidth) + 2)) / cardsForFour) <= 3) {
        const app = new App();
        app.moreVideos(token);
        maxRes += App.setMaxResults();
        cardsForFour += cardsForFour;
      }
    } else if (slider.offsetWidth === 960 && maxRes <= 60) {
      if (Math.round((maxRes - (Math.round(slider.scrollLeft
        / slider.offsetWidth) + 1)) / cardsForThree) <= 3) {
        const app = new App();
        app.moreVideos(token);
        maxRes += App.setMaxResults();
        cardsForThree += cardsForThree;
      }
    } else if (slider.offsetWidth === 640 && maxRes <= 60) {
      if (Math.round((maxRes - (Math.round(slider.scrollLeft
        / slider.offsetWidth) + 1)) / cardsForTwo) <= 4) {
        const app = new App();
        app.moreVideos(token);
        maxRes += App.setMaxResults();
        cardsForTwo += cardsForTwo;
      }
    } else if (slider.offsetWidth === 320 && maxRes <= 60) {
      if (Math.round((maxRes - (Math.round(slider.scrollLeft
        / slider.offsetWidth) + 1)) / cardsForOne) <= 1) {
        const app = new App();
        app.moreVideos(token);
        maxRes += App.setMaxResults();
        cardsForOne += cardsForOne;
      }
    }
  }

  static slider(token) {
    const slider = document.getElementById('container');
    let isDown = false;
    let startX;
    slider.addEventListener('mousedown', (e) => {
      isDown = true;
      slider.style.cursor = 'grabbing';
      startX = e.pageX - slider.offsetLeft;
    });

    slider.addEventListener('mouseleave', () => {
      isDown = false;
      slider.style.cursor = 'default';
    });

    slider.addEventListener('mouseup', () => {
      isDown = false;
      slider.style.cursor = 'default';
      App.renderNewVideos(token);
    });

    slider.addEventListener('mousemove', (e) => {
      if (isDown) {
        e.preventDefault();
        const x = e.pageX - slider.offsetLeft;
        const walk = (x - startX);
        if (walk < 0) {
          slider.scrollLeft += slider.offsetWidth;
        } else {
          slider.scrollLeft -= slider.offsetWidth;
        }
      }
    });
  }

  static buttonsSlider(token) {
    const buttonLeft = document.querySelector('.button-left');
    const buttonRight = document.querySelector('.button-right');
    const slider = document.getElementById('container');
    const currentState = document.querySelector('.stateCircle');

    currentState.addEventListener('mouseover', () => {
      document.querySelector('.tooltip-center').innerHTML = `<p>${Math.round(slider.scrollLeft / slider.offsetWidth) + 1}</p>`;
    });

    buttonLeft.addEventListener('click', () => {
      slider.scrollLeft -= slider.offsetWidth;
    });

    buttonRight.addEventListener('click', () => {
      slider.scrollLeft += slider.offsetWidth;
      App.renderNewVideos(token);
    });
  }

  async start() {
    if (document.querySelector('.container')) {
      AppView.delete();
      AppView.deleteButtons();
    }
    const state = {
      url: (this.state.url).concat('search?', App.getApiKey(), '&part=snippet&type=video&',
        `maxResults=${App.setMaxResults()}`, '&q=', App.getInputValue()),
    };
    let model = new AppModel(state);
    const title = await model.getClipNames();
    const image = await model.getClipImages();
    const chTitle = await model.getChannelNames();
    const date = await model.getClipDate();
    const description = await model.getDescription();
    const id = await model.getClipId();
    const token = await model.getToken();
    const stateForViews = {
      url: (this.state.url).concat('videos?', App.getApiKey(), '&id=', id, '&part=snippet,statistics'),
    };
    model = new AppModel(stateForViews);
    const views = await model.getClipViews();
    const view = new AppView(title, image, chTitle, date, views, description,
      id, App.setMaxResults());
    AppView.createContainers();
    view.render();
    App.slider(token);
    AppView.createButtons();
    App.buttonsSlider(token);
  }

  async moreVideos(token) {
    const newURL = {
      url: (this.state.url).concat('search?', App.getApiKey(), '&part=snippet&type=video&',
        `maxResults=${App.setMaxResults()}`, '&q=', App.getInputValue()).concat(`&pageToken=${token}`),
    };
    let model = new AppModel(newURL);
    const title = await model.getClipNames();
    const image = await model.getClipImages();
    const chTitle = await model.getChannelNames();
    const date = await model.getClipDate();
    const description = await model.getDescription();
    const id = await model.getClipId();
    const tokenNext = await model.getToken();
    const stateForViews = {
      url: (this.state.url).concat('videos?', App.getApiKey(), '&id=', id, '&part=snippet,statistics'),
    };
    model = new AppModel(stateForViews);
    const views = await model.getClipViews();
    const view = new AppView(title, image, chTitle, date, views, description,
      id, App.setMaxResults());
    view.render();
    App.slider(tokenNext);
  }
}
